      <md-card-header>
        <div class="md-title">erster Test - v-if</div>
      </md-card-header>
      <md-card-content>
        <div v-if="hasNotStarted('2018-09-22 18:30:00Z')">
          True
        </div>
        <div v-else>
          False
        </div>
        
      </md-card-content>
    </md-card>
    <!--
    <br/>
    <md-card>
      <md-card-header>
        <div class="md-title">zweiter Test - mit :value und type=number</div>
      </md-card-header>
