      </md-card-content>
    </md-card>
    <br/>
    <md-card>
      <md-card-header>
        <div class="md-title">dritter Test - mit :value und ohne type</div>
      </md-card-header>
      <md-card-content>
        <input id="12345_1" maxlength="1" class="tippInput" :value="match.tipp_away" />
      </md-card-content>
    </md-card>
    
    <br/>
    <md-card>
      <md-card-header>
