    </md-card>
    -->
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      match: {
        tipp_home: 1,
        tipp_away: 2
      }
    }
  },
  methods: {
    hasNotStarted(dt) {
            var rightNow = new Date();
            return true;
            var utc = new Date(rightNow.getTime() + rightNow.getTimezoneOffset() * 60000);

            var matchdt = new Date(dt);
            return utc<matchdt;
        }
  }
}
</script>

<style>
.tippInput{
    min-width: 5px;
    max-width: 35px;
}
</style>


